.user {
  min-height: 85px;
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: var(--border03);
  border-radius: 10px;
  color: rgba(255, 255, 255, 0.5);
  transition: background-color 0.3s;
}
.user:hover {
    background-color: var(--hover-color);
}
.userInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 70%;
  max-width: 70%;
  gap: 7px;
  overflow: hidden;
}
.userAvaDiv {
  overflow: hidden;
  position: relative;
  min-width: 70px;
  height: 70px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
}
.userAvaDiv:hover .imgOpenbtn {
  transform: translateY(-25px);
}
.userAva {
    width: 70px;
    height: 70px;
    border-radius: 50%;
}
.imgOpenbtn {
  width: 100%;
  height: 25px;
  position: absolute;
  bottom: -25px;
  background-color: rgba(255, 255, 255, 0.7);
  background-image: url('./../../../photos/lupa.svg');
  background-position: center;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  transition: all 0.1s;
}
.imgOpenbtn:hover{
  background-size: 23px 23px;

}
.userNameAndStatusDiv {
  display: flex;
  flex-direction: column;
  gap: 5px;
  overflow: hidden;
}
.userName {
  transition: all 0.2s;
  font-weight: 400;
  font-size: 18px;
  margin: 0;

}
.userStatus {
  overflow: hidden;
  font-size: 16px;
  white-space: nowrap;
  font-weight: 200;
  text-overflow: ellipsis;
  margin: 0;
}
.userLink {
  text-decoration: none;
  color: var(--text-color);
  
}

.followBtn {
  border: var(--border03);
background-color: rgba(0, 0, 0, 0);;
  color: var(--color);
  border-radius: 5px;
  width: 85px;
  height: 50px;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.followBtn:active{
  transform: scale(0.9);
}